	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/pokeproject-dev-diary-day-five-92859457a20a/index.md absPath of file >>> MarkdownRemark","html":"<h1>PokéProject Dev Diary: Day Five</h1>\n<h2>Move fast, change things.</h2>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-2f776.jpeg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.64999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 Kp3dePIQiFvW6HuPg7Dxpg\"\n        title=\"\"\n        src=\"/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-af57e.jpeg\"\n        srcset=\"/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-847f7.jpeg 158w,\n/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-93973.jpeg 315w,\n/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-af57e.jpeg 630w,\n/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-28cf9.jpeg 945w,\n/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-0d474.jpeg 1260w,\n/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-66f39.jpeg 1890w,\n/portfolio-2018/static/1*Kp3dePIQiFvW6HuPg7Dxpg-2d83f5bd82787c33023f29310ff911a6-2f776.jpeg 2000w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Cover image by Alice Achterhof / Unsplash: <a href=\"https://unsplash.com/search/painting?photo=FwF_fKj5tBo\">https://unsplash.com/search/painting?photo=FwF_fKj5tBo</a></p>\n<p><a href=\"https://www.pokeproject.co.uk/\">PokéProject</a> has been live for just over a day now, and I’ve had some initial feedback from early users.</p>\n<p>Amongst that feedback was updating the design to include gradients, much like those provided by the <a href=\"https://sarcadass.github.io/granim.js/\">Granim library.</a> Other bits of feedback have been that it’s frustrating, not being able to download or share the generated images, and that the sprites don’t quite scale properly on browsers other than Chrome.</p>\n<p>Armed with this feedback I’ve completely recreated the visuals using the HTML5 Canvas element instead of simple DOM elements; gone are the <strong><div></strong>, <strong><img /></strong> and CSS elements to be replaced by a<strong><canvas /></strong> element, featuring gradients, sprites and web fonts.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/1*BSQJuXDTSgvqHeDOYcSNjw-8e7d8b689f71cbf9f62e1241822076a7-58fd0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 BSQJuXDTSgvqHeDOYcSNjw\"\n        title=\"\"\n        src=\"/portfolio-2018/static/1*BSQJuXDTSgvqHeDOYcSNjw-8e7d8b689f71cbf9f62e1241822076a7-082ce.png\"\n        srcset=\"/portfolio-2018/static/1*BSQJuXDTSgvqHeDOYcSNjw-8e7d8b689f71cbf9f62e1241822076a7-c7cb1.png 158w,\n/portfolio-2018/static/1*BSQJuXDTSgvqHeDOYcSNjw-8e7d8b689f71cbf9f62e1241822076a7-93041.png 315w,\n/portfolio-2018/static/1*BSQJuXDTSgvqHeDOYcSNjw-8e7d8b689f71cbf9f62e1241822076a7-082ce.png 630w,\n/portfolio-2018/static/1*BSQJuXDTSgvqHeDOYcSNjw-8e7d8b689f71cbf9f62e1241822076a7-58fd0.png 800w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/1*jGwMtgUtaZqwVs3rUQhi2Q-5a4974e5ff2d11a9ac8f1b23c90d9cab-58fd0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 jGwMtgUtaZqwVs3rUQhi2Q\"\n        title=\"\"\n        src=\"/portfolio-2018/static/1*jGwMtgUtaZqwVs3rUQhi2Q-5a4974e5ff2d11a9ac8f1b23c90d9cab-082ce.png\"\n        srcset=\"/portfolio-2018/static/1*jGwMtgUtaZqwVs3rUQhi2Q-5a4974e5ff2d11a9ac8f1b23c90d9cab-c7cb1.png 158w,\n/portfolio-2018/static/1*jGwMtgUtaZqwVs3rUQhi2Q-5a4974e5ff2d11a9ac8f1b23c90d9cab-93041.png 315w,\n/portfolio-2018/static/1*jGwMtgUtaZqwVs3rUQhi2Q-5a4974e5ff2d11a9ac8f1b23c90d9cab-082ce.png 630w,\n/portfolio-2018/static/1*jGwMtgUtaZqwVs3rUQhi2Q-5a4974e5ff2d11a9ac8f1b23c90d9cab-58fd0.png 800w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/1*jK61EEwBmiAzn50G0ni6Jw-a242784c3b4a99b434ccda2754d97231-58fd0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 jK61EEwBmiAzn50G0ni6Jw\"\n        title=\"\"\n        src=\"/portfolio-2018/static/1*jK61EEwBmiAzn50G0ni6Jw-a242784c3b4a99b434ccda2754d97231-082ce.png\"\n        srcset=\"/portfolio-2018/static/1*jK61EEwBmiAzn50G0ni6Jw-a242784c3b4a99b434ccda2754d97231-c7cb1.png 158w,\n/portfolio-2018/static/1*jK61EEwBmiAzn50G0ni6Jw-a242784c3b4a99b434ccda2754d97231-93041.png 315w,\n/portfolio-2018/static/1*jK61EEwBmiAzn50G0ni6Jw-a242784c3b4a99b434ccda2754d97231-082ce.png 630w,\n/portfolio-2018/static/1*jK61EEwBmiAzn50G0ni6Jw-a242784c3b4a99b434ccda2754d97231-58fd0.png 800w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Using a canvas element has several advantages — the resulting images can be easily saved, the gradients are always smooth, and the scaling of the sprites can be tightly controlled, avoiding any unwanted blurring.</p>\n<p>Such a significant change takes a little while, so that’s about all I’ve managed for today’s development session.</p>\n<p>One final thing for the day is that I’ve now set up a proper backlog on <a href=\"https://trello.com/\">Trello</a> to keep track of my future development ideas and feedback from users.</p>\n<h4>Retrospective</h4>\n<p>What’s next? Rewriting everything in canvas was a bit of a big step for the second day that the website had been live, so perhaps we’ll not be doing that again in a hurry.</p>\n<p>The next big thing is really to get some traction in Google rankings and drive some traffic towards PokéProject, and for that we need to start considering things like SEO. Next up then, is some SEO work.</p>\n<hr>\n<p>PokéProject is available online @ <a href=\"https://www.pokeproject.co.uk\">https://www.pokeproject.co.uk</a></p>","frontmatter":{"title":"PokéProject Dev Diary: Day Five","date":"September 20, 2016","tags":null,"url":"https://medium.com/@psyked/pok%C3%A9project-dev-diary-day-five-92859457a20a"}}},"pathContext":{}}

